// import VisualComponent from "jx/core/comps/VisualComponent"
import DataGroup from "jx/editor/editionitems/DataGroup";
export default class QuizDataItem extends DataGroup {
    constructor(dataObject) {
        super(dataObject);
        this.controller = this.dataObject.controller;
        this.data.exportType = "DataGroup";
        this.view.append($("<div><div id='children'></div></div>"));
        this.childrenContainer = this.view.find("#children");
        this.btnConfig = this.cc({ type: "editionitems.ButtonItem", data: { id: "btnConfig", title: "Configurer le quiz", confirmBeforeLaunch: false, onclick: () => this.configure() } });
        this.childrenContainer.prepend(this.btnConfig.view);
        this._infos = {
            title: this.data.title,
            thema: null,
            style: null,
            questionsNum: null,
            responsesNum: null
        };
        this.children = [];
        this.questionsArr = [];
        this._themaOptions = [
            { id: "ART_CULTURE", desc: "ART ET CULTURE" },
            { id: "HISTOIRE", desc: "HISTOIRE" },
            { id: "GEOGRAPHIE", desc: "GEOGRAPHIE" },
            { id: "NATURE", desc: "NATURE" },
            { id: "SCIENCES_TECHNIQUE", desc: "SCIENCES ET TECHNIQUE" },
            { id: "SOCIETE", desc: "SOCIETE" },
            { id: "LANGUE", desc: "LANGUE" },
            { id: "SPORT", desc: "SPORT" }
        ];
        if (this.data.children.length == 0)
            this.addInfos();
    }
    showInfo() { }
    hideInfo() { }
    clearAll({ confirm = true }) {
        console.log(this.questionsArr);
        if (confirm) {
            this.jx.editor.confirm({ content: "Supprimer toutes les questions ?", onclose: evt => {
                    if (evt.action == "ok") {
                        for (var i = 0; i < this.questionsArr.length; i++) {
                            this.questionsArr[i].view.remove();
                        }
                        this.questionsArr.length = 0;
                        this.questionsContainer.data.children.length = 0;
                        this.questionsContainer.onchanged.dispatch({ target: this.questionsContainer });
                    }
                } });
        }
        else {
            for (var i = 0; i < this.questionsArr.length; i++) {
                this.questionsArr[i].view.remove();
            }
            this.questionsArr.length = 0;
            this.questionsContainer.data.children.length = 0;
            this.questionsContainer.onchanged.dispatch({ target: this.questionsContainer });
        }
    }
    get questionsNum() {
        return this.questionsArr.length;
    }
    addChild(item) {
        this.childrenContainer.append(item.view);
        this.children.push(item);
    }
    addInfos() {
        // let infos = this.addDataObject(this, "infos", showHeader=true);
        // let questions = this.addDataGroup(this, "questions", showHeader=true);
        let data = [
            {
                "id": "infos",
                "type": "DataObject",
                "description": "",
                "visible": true,
                "children": [
                    {
                        "id": "thema",
                        "title": "Thème",
                        "type": "ComboBox",
                        "editable": true,
                        "description": "",
                        "value": this._infos.thema,
                        "options": this._themaOptions
                    },
                ]
            },
            {
                "id": "questions",
                "type": "DataGroup",
                "description": "",
                "visible": true,
                "children": []
            }
        ];
        this.data.children = data;
    }
    get questionsContainer() {
        if (this._questionContainer)
            return this._questionContainer;
        for (var i = 0; i < this.children.length; i++) {
            if (this.children[i].id == "questions") {
                this._questionContainer = this.children[i];
                return this._questionContainer;
            }
        }
        return null;
    }
    addQuestion(infos) {
        console.log(this.data);
        let noQuestion = this.questionsNum + 1;
        let data = {
            "id": "Q" + noQuestion,
            "title": "<span class='qz-question'>Q" + noQuestion + "</span>",
            "type": "DataGroup",
            "description": "QUESTION " + noQuestion,
            "visible": true,
            "children": []
        };
        this.questionsContainer.data.children.push(data);
        let question = this.questionsContainer.cc({ type: "editionitems.DataGroup", data });
        question.onchanged.add(() => {
            this.questionsContainer.onchanged.dispatch({ target: this.questionsContainer });
            // this.onchanged.dispatch({target: this});
        });
        question.no = noQuestion;
        this.questionsContainer.view.append(question.view);
        this.questionsArr.push(question);
        if (infos.questionText)
            this.addText(question);
        if (infos.questionImage)
            this.addImage(question, this.id + "/images/Q" + question.no + ".png");
        if (infos.questionSound)
            this.addSound(question, this.id + "/sounds/Q" + question.no + ".mp3");
        let responses = this.addDataGroup(question, "responses", false);
        for (var i = 0; i < infos.responsesNum; i++) {
            let response = this.addDataGroup(responses, "Q" + question.no + "_R" + (i + 1));
            response.no = i + 1;
            if (infos.responseText)
                this.addText(response);
            if (infos.responseImage)
                this.addImage(response, this.id + "/images/Q" + question.no + "_R" + response.no + ".png");
            if (infos.responseSound)
                this.addSound(response, this.id + "/sounds/Q" + question.no + "_R" + response.no + ".mp3");
        }
        ;
        this.questionsContainer.onchanged.dispatch({ target: this.questionsContainer });
        //this.onchanged.dispatch({target: this});
    }
    addDataObject(parent, id, showHeader = true) {
        let data = {
            "id": id,
            "type": "DataObject",
            "showHeader": showHeader,
            "description": "",
            "visible": true,
            "children": []
        };
        parent.data.children.push(data);
        let item = parent.cc({ type: "editionitems.DataGroup", data });
        parent.view.append(item.view);
        return item;
    }
    addDataGroup(parent, id, showHeader = true) {
        let data = {
            "id": id,
            "type": "DataGroup",
            "showHeader": showHeader,
            "description": "",
            "visible": true,
            "children": []
        };
        parent.data.children.push(data);
        let item = parent.cc({ type: "editionitems.DataGroup", data });
        parent.view.append(item.view);
        return item;
    }
    addText(question) {
        let data = {
            "id": "text",
            "type": "Text",
            "editable": true,
            "description": "",
            "value": "",
            "visible": true
        };
        let text = this.cc({ type: "editionitems.Text", data });
        text.onchanged.add(() => {
            this.questionsContainer.onchanged.dispatch({ target: this.questionsContainer });
            //this.onchanged.dispatch({target: this});
        });
        question.data.children.push(data);
        question.view.append(text.view);
    }
    addImage(question, src) {
        let data = {
            "id": "image",
            "type": "Image",
            "editable": true,
            "description": "",
            "asset": {
                "preload": false,
                "src": src
            },
            "visible": true
        };
        let text = this.cc({ type: "editionitems.ImageItem", controller: this.controller, data });
        text.onchanged.add(() => {
            this.questionsContainer.onchanged.dispatch({ target: this.questionsContainer });
            //this.onchanged.dispatch({target: this});
        });
        question.data.children.push(data);
        question.view.append(text.view);
    }
    addSound(question, src) {
        let data = {
            "id": "sound",
            "type": "Sound",
            "editable": true,
            "description": "",
            "asset": {
                "preload": false,
                "src": src
            },
            "visible": true
        };
        let text = this.cc({ type: "editionitems.SoundItem", controller: this.controller, data });
        text.onchanged.add(() => {
            this.questionsContainer.onchanged.dispatch({ target: this.questionsContainer });
            //this.onchanged.dispatch({target: this});
        });
        question.data.children.push(data);
        question.view.append(text.view);
    }
    addItem() {
        console.log(this.data);
        this.data.children = this.data.children || [];
        let noQuestion = this.questionsNum;
        let data = {
            "id": "q" + noQuestion,
            "title": "Question" + noQuestion,
            "type": "editionitems.Text",
            "editable": true,
            "description": "description.",
            "value": "",
            "visible": true
        };
        this.data.children.push(data);
        let text = this.cc({ type: data.type, data });
        text.onchanged.add(() => {
            this.questionsContainer.onchanged.dispatch({ target: this.questionsContainer });
            //this.onchanged.dispatch({target: this});
        });
        this.childrenContainer.append(text.view);
        this.onchanged.dispatch({ target: this });
        //this.data.value.resourceID = "yo";
    }
    configure({ mess } = {}) {
        console.log("configure", this.questionsArr);
        let editionPage = this.controller.mainController;
        let gabaritView = editionPage.gabaritView;
        console.log(gabaritView.value);
        let content = `
        <form class="form-horizontal">
            <div id="mess" class="alert alert-danger" style="display:none"></div>
            <div class="form-group">    
            <label class="control-label col-sm-2" for="clearAll"></label>
            <div class="col-sm-10  text-right">
                <button id="clearAll" class="btn btn-danger" type="button">Vider ce quiz</button>
            </div>
           </div>

            <div class="form-group">    
            <label class="control-label col-sm-2" for="deleteQuiz"></label>
            <div class="col-sm-10  text-right">
                <button id="deleteQuiz" class="btn btn-danger" type="button">Supprimer ce quiz</button>
            </div>
           </div>
           <div class="form-group">    
                <label class="control-label col-sm-2" for="quizName">Nom du quiz</label>
                <div class="col-sm-10">
                <input  id="quizName" type="text"  class="form-control"  placeholder="Nom du quiz" value="${this.data.title || ""}"/>
                </div>
            </div>
            <div class="form-group">    
                <label class="control-label col-sm-2" for="quizThema">Thème</label>
                <div class="col-sm-10">
                    <select class="form-control" id="quizThema"/>
                </div>
            </div>
            <div class="form-group">    
                <label class="control-label col-sm-2" for="quizStyle">Style</label>
                <div class="col-sm-10">
                    <select class="form-control" id="quizStyle"/>
                </div>
            </div>
            <div class="form-group">    
                <label class="control-label col-sm-2" for="questionsNum">Nombre de Questions</label>
                <div class="col-sm-10">
                    <select class="form-control" id="questionsNum"/>
                </div>
            </div>
           <div class="form-group">    
                <label class="control-label col-sm-2" for="responsesNum">Nombre de Réponses</label>
                <div class="col-sm-10">
                    <select class="form-control" id="responsesNum"/>
                </div>
            </div>
        </form>
            `;
        let popup = this.jx.editor.popup({ title: "New Quiz", content, width: 600, buttons: { ok: {}, no: {} }, onclose: evt => {
                console.log(evt);
                if (evt.action != "ok")
                    return;
                console.log($(popup).find("#quizName")[0].value);
                console.log($(popup).find("#quizThema")[0].value);
                console.log($(popup).find("#quizStyle")[0].value);
                // this.uniqTabID = Date.now();
                // let idTab = "QuizData_" + this.uniqTabID;
                let find = (id) => {
                    return $(popup).find("#" + id)[0];
                };
                this._changeQuiz({
                    name: find("quizName").value,
                    thema: find("quizThema").value,
                    style: find("quizStyle").value,
                    questionsNum: find("questionsNum").value,
                    responsesNum: find("responsesNum").value
                });
            } });
        $(popup).find("#deleteQuiz").on("click", () => {
            popup.modal('hide');
            this.deleteQuiz();
        });
        $(popup).find("#clearAll").on("click", () => {
            popup.modal('hide');
            this.clearAll({ comfirm: true });
        });
        if (mess) {
            $(popup).find("#mess").html(mess);
            $(popup).find("#mess").css("display", "inline");
        }
        ;
        this._fillComboBox({ formEl: $(popup).find("#quizStyle"), options: [
                { id: "q1", desc: "Q1: Question image + texte, Réponses texte" },
                { id: "q2", desc: "Q2: Question texte, Réponses texte", enabled: false }
            ],
            selected: this._infos.style
        });
        this._fillComboBox({ formEl: $(popup).find("#questionsNum"), options: [
                { id: 3 },
                { id: 5 },
                { id: 10 }
            ],
            selected: this._infos.questionsNum
        });
        this._fillComboBox({ formEl: $(popup).find("#responsesNum"), options: [
                { id: "truefalse", desc: "Pictos Oui Non" },
                { id: 2 },
                { id: 3 },
                { id: 4 }
            ],
            selected: this._infos.responsesNum
        });
        this._fillComboBox({ formEl: $(popup).find("#quizThema"), options: [
                { id: "ART_CULTURE", desc: "ART ET CULTURE" },
                { id: "HISTOIRE", desc: "HISTOIRE" },
                { id: "GEOGRAPHIE", desc: "GEOGRAPHIE" },
                { id: "NATURE", desc: "NATURE" },
                { id: "SCIENCES_TECHNIQUE", desc: "SCIENCES ET TECHNIQUE" },
                { id: "SOCIETE", desc: "SOCIETE" },
                { id: "LANGUE", desc: "LANGUE" },
                { id: "SPORT", desc: "SPORT" }
            ],
            selected: this._infos.thema
        });
    }
    _changeQuiz({ name, thema, style, questionsNum, responsesNum }) {
        console.log("infoquiz", this._infos);
        console.log("configure_changeQ", this.questionsArr);
        console.log("infoquiz2", name, thema, style, questionsNum, responsesNum);
        let recreateQuiz = false;
        if (style != this._infos.style) {
            this._infos.style = style;
            recreateQuiz = true;
        }
        if (questionsNum != this._infos.questionsNum) {
            this._infos.questionsNum = questionsNum;
            recreateQuiz = true;
        }
        if (thema != this._infos.thema) {
            this._infos.thema = thema;
        }
        if (style != this._infos.style) {
            this._infos.style = style;
        }
        if (responsesNum != this._infos.responsesNum) {
            this._infos.responsesNum = responsesNum;
            recreateQuiz = true;
        }
        if (recreateQuiz) {
            if (this._infos.questionsNum == undefined) {
                this.configure({ mess: "Indiquez le nombre de questions" });
            }
            ;
            if (this._infos.style == "q1") {
                this._infos.questionText = true;
                this._infos.questionImage = true;
                this._infos.questionSound = false;
                this._infos.responseText = true;
                this._infos.responseImage = false;
                this._infos.responseSound = false;
            }
            else {
                this._infos.questionText = true;
                this._infos.questionImage = true;
                this._infos.questionSound = false;
                this._infos.responseText = true;
                this._infos.responseImage = false;
                this._infos.responseSound = false;
            }
            this.jx.editor.confirm({ title: "Création du Quiz", content: "Attention ! \nCeci va supprimer les données existantes.\n Voulez-vous continuer ?", onclose: evt => {
                    if (evt.action == "ok") {
                        this.clearAll({ confirm: false });
                        //this.addInfos();
                        for (var i = 0; i < this._infos.questionsNum; i++) {
                            this.addQuestion(this._infos);
                        }
                        this.questionsContainer.onchanged.dispatch({ target: this.questionsContainer });
                        // this.onchanged.dispatch({target: this});
                    }
                    ;
                    if (name != this.data.title) {
                        this.setTitle(name);
                        this.questionsContainer.onchanged.dispatch({ target: this.questionsContainer });
                        // this.onchanged.dispatch({target: this});
                        let editionPage = this.controller.mainController;
                        let gabaritView = editionPage.gabaritView;
                        editionPage.refreshEditionView();
                        editionPage.showTab(this.id);
                    }
                    console.log("configure2", this.questionsArr);
                } });
        }
        ;
    }
    _fillComboBox({ formEl, options, selected }) {
        for (var i = 0; i < options.length; i++) {
            var opt = options[i];
            var el = document.createElement("option");
            el.textContent = opt.desc || opt.id;
            el.value = opt.id;
            $(el).attr("selected", selected ? (opt.id == selected) : (opt.id == options[0].value));
            $(el).attr("disabled", (opt.enabled == undefined ? false : !opt.enabled));
            formEl.append(el);
        }
    }
    deleteQuiz() {
        this.jx.editor.confirm({ title: "Suppression du Quiz", content: "Attention ! \nCeci va supprimer définitivement ce Quiz.\n Voulez-vous continuer ?", onclose: evt => {
                if (evt.action == "ok") {
                    let editionPage = this.controller.mainController;
                    editionPage.deleteTab(this.id);
                }
                ;
            } });
    }
    set lock(value) {
        console.info("lock", value, this.btnConfig.editable);
        if (this.btnConfig)
            this.btnConfig.lock = value;
        // if(!value){
        //     this.view.find("#value").prop('disabled', !this.editable); 
        // }
        // else {
        //     this.view.find("#value").prop('disabled', true)
        // }
    }
}
